export let fetchCheckins = (userId) =>
    fetch('/api/getMyCheckins', {
        method: 'POST',
        headers: {
            'Content-Type':'text/plain'
        },
        body: String(userId)
    })
    .then(res => res.json());

export let fetchUpdateFriends = (userid, newFriendsArray) =>
    fetch('/api/updateFriends', {
        method: 'POST',
        headers: {
            'Content-Type':'application/json'
        },
        body: JSON.stringify({
            userid: userid,
            friendsarray: JSON.stringify(newFriendsArray)
        })
    })



